import React, { useState, useRef } from 'react';
import profileImage from './images/profile-image-zahn-center.jpg';
import { FileText, Linkedin, Phone, Mail, ArrowRight, ArrowLeft } from 'react-feather';
import DiagramDecor from './images/diagram-decor.svg';
import CircleDecor from './images/circle-decor.svg';
import SquaresDecor from './images/squares-decor.svg';
import XIcon from './images/x-icon.svg';
import HamburgerIcon from './images/hamburger-icon.svg';
import Logo from './images/arif-lockup.svg';
import './scss/main.scss';
import Kinetic from './components/kinetic/kinetic';
import Zahn from './components/zahn/zahn';
import CityCollege from './components/city-college/city-college';

const scrollToRefObject = (ref) => window.scrollTo(0, ref.current.offsetTop);

function App() {
  const [expItem, setExpItem] = useState('KINETIC');

  const hero = useRef(null);
  const experience = useRef(null);
  const contact = useRef(null);

  const scrollToRef = (ref) => scrollToRefObject(ref);
  const scrollToItem = (ref, cf) => {
    if (cf)
      cf();

    if (showNavbar)
      setShowNavbar(false);
    
    scrollToRef(ref);
  }

  const [showNavbar, setShowNavbar] = useState(false);

  return (
    <div className="main-app">
      <div className = "hero" ref = { hero }>
        <header className="header space-between">
          <a 
            href = "/" 
            className = "logo"
            onClick = { 
              (e) => { 
                e.preventDefault(); 
                scrollToRef(hero)  
              }
            }
          >
            <img src={ Logo } alt="logo"/>
            <span style = {{ display: 'none' }}>
              M. Arif
            </span>
          </a>

          <div className={ (showNavbar) ? `sub-nav open` : `sub-nav` }>
            <span
              onClick = { 
                () => scrollToItem(
                  experience, 
                  () => setExpItem('KINETIC')
                ) 
              }
            >
              KINETIC
            </span>

            <span
              onClick = { 
                () => scrollToItem(
                  experience, 
                  () => setExpItem('CITY_COLLEGE')
                ) 
              }
            >
              CCNY
            </span>

            <span
              onClick = { 
                () => scrollToItem(
                  experience, 
                  () => setExpItem('ZAHN')
                ) 
              }
            >
              Zahn
            </span>

            <span
              onClick = { () => scrollToRef(contact) }
            >
              Contact
            </span>
          </div>

          <button 
            className = "navbar-button"
            onClick = { () => setShowNavbar(!showNavbar) }
          >
              { (!showNavbar)
                ? (<img src = { HamburgerIcon } alt = "Hamburger Menu" />)
                : (<img src = { XIcon } alt = "Exit Menu" />)
              }
          </button>
        </header>

        <div className="about-me">
          <div className="inner-content side-by-side">
            <img src={ profileImage } alt="profile image" className = "profile-image" />
            
            <div className = "details">
              <h1 className="name">
                Mohammad Arif
              </h1>
              <h2>
                <span>
                  Mechanical Engineering
                </span>
                <span className = "school">
                  The City College of New York
                </span>
              </h2>

              <p className = "desc">
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam sapiente placeat inventore nemo a ad eos, deserunt consectetur nostrum dignissimos tempora ullam vero tenetur eius quam cupiditate fugiat, accusantium impedit.
              </p>

              <div className="cta-container">
                <a href="resume" className = "resume">
                  <div className="inner">  
                    <FileText />
                    <span>Resume</span>
                  </div>
                </a>

                <a href="" className="linked-in">
                  <div className="inner">
                    <Linkedin />
                    <span>LinkedIn</span>
                  </div>
                </a>
              </div>
            </div>
          </div>
        </div>
        <img src={DiagramDecor} alt="" className = "svg-1" />
        <img src={CircleDecor} alt="" className = "svg-2" />
      </div>

      <div className = "experience" id = "experience" ref = { experience }>
        <h1 className = "main-header">
          Experience
        </h1>
        <div className="inner-content side-by-side">
          <div className="side-bar">
              <ul>
                <li 
                  className = { (expItem === 'KINETIC') ? 'selected' : ''}
                  onClick = { 
                    () => scrollToItem(
                      experience, 
                      () => setExpItem('KINETIC')
                    ) 
                  }
                >
                  KINETIC
                </li>
                <li 
                  className = { (expItem === 'CITY_COLLEGE') ? 'selected' : ''} 
                  onClick = { 
                    () => scrollToItem(
                      experience, 
                      () => setExpItem('CITY_COLLEGE')
                    ) 
                  }
                >
                  The City College of New York
                </li>
                <li 
                  className = { (expItem === 'ZAHN') ? 'selected' : ''} 
                  onClick = { 
                    () => scrollToItem(
                      experience, 
                      () => setExpItem('ZAHN')
                    )
                  }
                >
                  The Zahn Innovation Center
                </li>
              </ul>
            </div>
          <div className = "content">
              { (expItem === 'KINETIC') &&
                <Kinetic />
              }

              { (expItem === 'CITY_COLLEGE') &&
                <CityCollege />
              }

              { (expItem === 'ZAHN') &&
                <Zahn />
              }

              <div className="directions-btn side-by-side">
                <button
                  className="prev side-by-side"
                  onClick = { () => {
                    switch (expItem) {
                      case 'KINETIC':
                        scrollToItem(
                          experience, 
                          () => setExpItem('ZAHN')
                        )
                        break;
                      case 'CITY_COLLEGE':
                        scrollToItem(
                          experience, 
                          () => setExpItem('KINETIC')
                        )
                        break;
                      case 'ZAHN':
                        scrollToItem(
                          experience, 
                          () => setExpItem('CITY_COLLEGE')
                        )
                        break;
                    }
                  } }
                >
                  <ArrowLeft /> <span>Prev</span> 
                </button>
                <button
                  className="next side-by-side"
                  onClick = { () => {
                    switch (expItem) {
                      case 'KINETIC':
                        scrollToItem(
                          experience, 
                          () => setExpItem('CITY_COLLEGE')
                        )
                        break;
                      case 'CITY_COLLEGE':
                        scrollToItem(
                          experience, 
                          () => setExpItem('ZAHN')
                        )
                        break;
                      case 'ZAHN':
                        scrollToItem(
                          experience, 
                          () => setExpItem('KINETIC')
                        )
                        break;
                    }
                  } }
                >
                  <span>Next</span> <ArrowRight />
                </button>
              </div>
            </div>
        </div>
      </div>

      <div className = "contact" ref = { contact }>
        <img src={ SquaresDecor } alt="" className = "decor"/>
        <div className="inner space-between">
          <h1>Get In Touch!</h1>

          <ul>
            <li className = "phone side-by-side">
              <Phone />
              <span>
                123-456-7890
              </span>
            </li>
            <li className = "e-mail side-by-side">
              <Mail />
              <span>
                marif@gmail.com
              </span>
            </li>
            <li className = "resume">
              <a href="#" className = "side-by-side">
                <FileText />
                <span>
                  Resume
                </span>
              </a>
            </li>
            <li className = "linked-in">
              <a href = "#" className = "side-by-side">
                <Linkedin />
                <span>
                  LinkedIn
                </span>
              </a>
            </li>
          </ul>
        </div>
        <div className="copyright">
          Copyright &copy; 2020 Mohammad Arif 
        </div>
      </div>
    </div>
  );
}

export default App;
